const intersection = () => {
  //
  const callback = (entries) => {
    entries.forEach((entry) => {
      if (entry.isIntersecting) {
        entry.target.classList.add('on');
        // console.log('');
        // observer  unobserve() 
        observer.unobserve(entry.target);
      } else {
        entry.target.classList.remove('on');
        //console.log('');
      }
    });
  };
  //
  const options = {
    root: null, //
    rootMargin: '-10% 0px',
    threshold: 0.1,
  };

  //
  const observer = new IntersectionObserver(callback, options);

  //
  const [...monitorElements] = document.querySelectorAll('.js-target');

  //observe()
  monitorElements.forEach((elem) => {
    //observe()
    observer.observe(elem);
  });
  //console.log('Intersection Observer API');
};
// export default intersection;

const timeline = () => {
  const timelineItems = document.querySelectorAll('.js-timeline-item');

  // Intersection Observer
  const observer = new IntersectionObserver(
    (entries) => {
      entries.forEach((entry, index) => {
        if (entry.isIntersecting) {
          // index
          setTimeout(() => {
            entry.target.classList.add('is-show');
          }, index * 400); // 1400ms(200ms)

          observer.unobserve(entry.target); // 
        }
      });
    },
    {
      threshold: 0.1, // 10%
    }
  );

  // 
  timelineItems.forEach((item) => {
    observer.observe(item);
  });
};

export { intersection, timeline };
